@import url('https://fonts.googleapis.com/css2?family=Be+Vietnam+Pro:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Oxygen:wght@300;400;700&display=swap');

*,
*::before,
*::after {
  box-sizing: inherit;
  font-family: "Be Vietnam Pro", sans-serif;
}

html {
  overflow-x: hidden;
  font-family: "Be Vietnam Pro", sans-serif;
  background-color: #fff7dd;
}

#root{
  background-color: #fff7dd;
}

body{
  margin: 0;
}
